exports.push([module.id, "\n.verify[data-v-4aa7d875] {\n  width: 100%;\n  height: 100vh;\n  background: #f8f8f8;\n}\n.header[data-v-4aa7d875] {\n  width: 100%;\n  height: 6.25rem;\n  overflow: hidden;\n}\n.header img[data-v-4aa7d875] {\n  float: left;\n  width: 100%;\n  height: auto;\n}\n.verify-content[data-v-4aa7d875] {\n  width: 100%;\n  height: calc(100vh - 6.9rem);\n  padding: 0.75rem 0.51rem;\n  -webkit-box-sizing: border-box;\n          box-sizing: border-box;\n}\n.verify-warpper[data-v-4aa7d875] {\n  padding: 1.225rem 1.3rem 1.325rem 1.45rem;\n  background: #fff;\n  border-radius: 0.4rem;\n  width: 100%;\n  /*height: 65vh;*/\n  -webkit-box-sizing: border-box;\n          box-sizing: border-box;\n}\n.verify-warpper-title[data-v-4aa7d875] {\n  font-size: 0.9rem;\n  color: rgba(51, 51, 51, 1);\n  margin-bottom: 1.35rem;\n}\n.verify-warpper-title i[data-v-4aa7d875] {\n  display: block;\n  width: 0.4rem;\n  height: 0.4rem;\n  border-radius: 50%;\n  background: #3366cc;\n  float: left;\n  margin-top: 0.45rem;\n}\n.verify-warpper-title span[data-v-4aa7d875] {\n  margin-left: 0.4rem;\n}\n.form-warpper[data-v-4aa7d875] {\n  position: relative;\n}\n.verify-warpper .form-warpper-p[data-v-4aa7d875] {\n  position: absolute;\n  color: red;\n  bottom: -15px;\n}\n.phoneInput[data-v-4aa7d875] {\n  width: 13.9rem;\n}\n.graphCodeInput[data-v-4aa7d875] {\n  width: 8.513rem;\n}\n.codeInput[data-v-4aa7d875] {\n  width: 13.9rem;\n}\n.verify-warpper-graphCode[data-v-4aa7d875] {\n  display: -webkit-box;\n  display: -ms-flexbox;\n  display: flex;\n  -webkit-box-pack: justify;\n      -ms-flex-pack: justify;\n          justify-content: space-between;\n  -webkit-box-align: center;\n      -ms-flex-align: center;\n          align-items: center;\n}\n.verify-warpper-graphCode img[data-v-4aa7d875] {\n  height: 1.375rem;\n  width: 4.5rem;\n  display: block;\n}\n.verify-warpper-Code[data-v-4aa7d875] {\n  position: relative;\n}\n.haveCode[data-v-4aa7d875] {\n  position: absolute;\n  right: 0.5rem;\n  top: 0.2rem;\n  color: #3366cc;\n  font-size: 0.8rem;\n}\n.inputall[data-v-4aa7d875] {\n  height: 2rem;\n  font-size: 0.8rem;\n  /*box-shadow: 0px 0px 4px 0px rgba(102,102,102,0.2);*/\n  border-radius: 4px;\n  font-family: Microsoft YaHei;\n  font-weight: 400;\n  padding: 0.1rem 0 0.1rem 0.85rem;\n}\n.phoneInputWay[data-v-4aa7d875] {\n  height: 2.125rem;\n}\n.verify-list-next[data-v-4aa7d875] {\n  text-align: center;\n  width: 14.75rem;\n  height: 2.5rem;\n  margin: 0 auto;\n  line-height: 2.5rem;\n  background: #eee;\n  font-size: 1rem;\n  color: #fff;\n  font-family: Microsoft YaHei;\n  font-weight: 400;\n  border-radius: 1.25rem;\n  margin-top: 2.5rem;\n}\n.verify-list-nextway[data-v-4aa7d875] {\n  margin-top: 60%;\n  background: #e4393c;\n}\n.erify-list-nextred[data-v-4aa7d875] {\n  background: #e4393c;\n}\n/* form表单 */\n.form_title[data-v-4aa7d875] {\n  font-size: 0.75rem;\n  line-height: 0.9rem;\n  padding-top: 1.2rem;\n}\n.verify-warpper .el-form-item[data-v-4aa7d875] {\n  padding: 0;\n  margin-bottom: 1rem;\n}\n.verify-warpper .input_style[data-v-4aa7d875] {\n  margin-top: 0.7rem;\n}\n.verify-warpper .input_style[data-v-4aa7d875] .el-input__inner {\n  height: 2.75rem;\n  padding: 0 0.7rem;\n  line-height: 2.75rem;\n  font-size: 0.8rem;\n}\n.form_title i[data-v-4aa7d875] {\n  color: #f00;\n  margin-left: 0.4rem;\n}\n.address_state[data-v-4aa7d875] {\n  display: block;\n  padding: 0.4rem 0.7rem;\n  line-height: 1.2rem;\n  font-size: 0.8rem;\n  border: 1px solid #e9e9e9;\n  background: #fff;\n  border-radius: 4px;\n  width: 14.75rem;\n  -webkit-box-sizing: border-box;\n          box-sizing: border-box;\n  position: relative;\n}\n.address_state b[data-v-4aa7d875] {\n  font-weight: normal;\n}\n.address_state .arrow_right[data-v-4aa7d875] {\n  position: absolute;\n  right: 0.4rem;\n  font-size: 1rem;\n  top: 50%;\n  margin-top: -0.5rem;\n}\n.address_state .tip[data-v-4aa7d875] {\n  color: #ccc8c8;\n  font-style: normal;\n}\n.warp_dialog[data-v-4aa7d875] {\n  width: 100%;\n  height: 100%;\n  position: fixed;\n  left: 0;\n  top: 0;\n}\n.warp_dialog .mark[data-v-4aa7d875] {\n  width: 100%;\n  height: 100%;\n  background: #000;\n  opacity: 0.4;\n}\n.dialog_content_address[data-v-4aa7d875] {\n  width: 100%;\n  height: 22rem;\n  position: absolute;\n  left: 0;\n  bottom: 0;\n  background: #fff;\n  border-radius: 0.2rem;\n}\n.show_selcet_address[data-v-4aa7d875] {\n  width: 100%;\n  color: #333;\n}\n.show_selcet_address[data-v-4aa7d875] {\n  width: 100%;\n  height: 5.8rem;\n  padding: 1rem 1rem;\n  -webkit-box-sizing: border-box;\n          box-sizing: border-box;\n  border-bottom: 1px solid #ccc;\n  overflow: hidden;\n}\n.show_selcet_address li[data-v-4aa7d875] {\n  float: left;\n  font-size: 0.6rem;\n  color: #333;\n  padding: 0.5rem 0.6rem;\n}\n.show_ullist_address[data-v-4aa7d875] {\n  width: 100%;\n  height: 14.2rem;\n  overflow-y: auto;\n  -webkit-box-sizing: border-box;\n          box-sizing: border-box;\n  padding: 1rem;\n}\n.show_ullist_address span[data-v-4aa7d875] {\n  display: inline-block;\n  padding: 0.3rem 0.6rem;\n  font-size: 0.7rem;\n  color: #666;\n  border: 1px solid #ccc;\n  border-radius: 2px;\n  margin: 0 0.6rem 0.4rem 0;\n}\n.footer-title[data-v-4aa7d875] {\n  -webkit-box-pack: center;\n      -ms-flex-pack: center;\n          justify-content: center;\n  -webkit-box-align: center;\n      -ms-flex-align: center;\n          align-items: center;\n  display: -webkit-box;\n  display: -ms-flexbox;\n  display: flex;\n  padding-bottom: 0.5rem;\n\n  margin-top: 1.15rem;\n  text-align: left;\n  color: rgba(153, 153, 153, 1);\n}\n.footer-title img[data-v-4aa7d875] {\n  width: 3.25rem;\n  margin-right: 0.15rem;\n}\n.footer-title-text[data-v-4aa7d875] {\n  font-size: 0.3rem;\n}\n.messTip[data-v-4aa7d875] {\n  position: fixed;\n  background: #000;\n  opacity: 0.4;\n  color: #fff;\n  height: 1.6rem;\n  font-size: 0.6rem;\n  line-height: 1.6rem;\n  top: 33%;\n  left: 30%;\n  padding: 0 1rem;\n  border-radius: 0.2rem;\n}\n", "", {"version":3,"sources":["/Users/shanshan/Desktop/vue-project/shenbing/src/components/organization/src/components/organization/verify.vue"],"names":[],"mappings":";AAsiBA;EACA,YAAA;EACA,cAAA;EACA,oBAAA;CACA;AACA;EACA,YAAA;EACA,gBAAA;EACA,iBAAA;CACA;AACA;EACA,YAAA;EACA,YAAA;EACA,aAAA;CACA;AACA;EACA,YAAA;EACA,6BAAA;EACA,yBAAA;EACA,+BAAA;UAAA,uBAAA;CACA;AACA;EACA,0CAAA;EACA,iBAAA;EACA,sBAAA;EACA,YAAA;EACA,iBAAA;EACA,+BAAA;UAAA,uBAAA;CACA;AACA;EACA,kBAAA;EACA,2BAAA;EACA,uBAAA;CACA;AACA;EACA,eAAA;EACA,cAAA;EACA,eAAA;EACA,mBAAA;EACA,oBAAA;EACA,YAAA;EACA,oBAAA;CACA;AACA;EACA,oBAAA;CACA;AACA;EACA,mBAAA;CACA;AACA;EACA,mBAAA;EACA,WAAA;EACA,cAAA;CACA;AACA;EACA,eAAA;CACA;AAEA;EACA,gBAAA;CACA;AACA;EACA,eAAA;CACA;AACA;EACA,qBAAA;EAAA,qBAAA;EAAA,cAAA;EACA,0BAAA;MAAA,uBAAA;UAAA,+BAAA;EACA,0BAAA;MAAA,uBAAA;UAAA,oBAAA;CACA;AACA;EACA,iBAAA;EACA,cAAA;EACA,eAAA;CACA;AACA;EACA,mBAAA;CACA;AACA;EACA,mBAAA;EACA,cAAA;EACA,YAAA;EACA,eAAA;EACA,kBAAA;CACA;AACA;EACA,aAAA;EACA,kBAAA;EACA,sDAAA;EACA,mBAAA;EACA,6BAAA;EACA,iBAAA;EACA,iCAAA;CACA;AACA;EACA,iBAAA;CACA;AACA;EACA,mBAAA;EACA,gBAAA;EACA,eAAA;EACA,eAAA;EACA,oBAAA;EACA,iBAAA;EACA,gBAAA;EACA,YAAA;EACA,6BAAA;EACA,iBAAA;EACA,uBAAA;EACA,mBAAA;CACA;AACA;EACA,gBAAA;EACA,oBAAA;CACA;AACA;EACA,oBAAA;CACA;AACA,YAAA;AACA;EACA,mBAAA;EACA,oBAAA;EACA,oBAAA;CACA;AACA;EACA,WAAA;EACA,oBAAA;CACA;AACA;EACA,mBAAA;CACA;AACA;EACA,gBAAA;EACA,kBAAA;EACA,qBAAA;EACA,kBAAA;CACA;AACA;EACA,YAAA;EACA,oBAAA;CACA;AACA;EACA,eAAA;EACA,uBAAA;EACA,oBAAA;EACA,kBAAA;EACA,0BAAA;EACA,iBAAA;EACA,mBAAA;EACA,gBAAA;EACA,+BAAA;UAAA,uBAAA;EACA,mBAAA;CACA;AACA;EACA,oBAAA;CACA;AACA;EACA,mBAAA;EACA,cAAA;EACA,gBAAA;EACA,SAAA;EACA,oBAAA;CACA;AACA;EACA,eAAA;EACA,mBAAA;CACA;AACA;EACA,YAAA;EACA,aAAA;EACA,gBAAA;EACA,QAAA;EACA,OAAA;CACA;AACA;EACA,YAAA;EACA,aAAA;EACA,iBAAA;EACA,aAAA;CACA;AACA;EACA,YAAA;EACA,cAAA;EACA,mBAAA;EACA,QAAA;EACA,UAAA;EACA,iBAAA;EACA,sBAAA;CACA;AACA;EACA,YAAA;EACA,YAAA;CACA;AACA;EACA,YAAA;EACA,eAAA;EACA,mBAAA;EACA,+BAAA;UAAA,uBAAA;EACA,8BAAA;EACA,iBAAA;CACA;AACA;EACA,YAAA;EACA,kBAAA;EACA,YAAA;EACA,uBAAA;CACA;AACA;EACA,YAAA;EACA,gBAAA;EACA,iBAAA;EACA,+BAAA;UAAA,uBAAA;EACA,cAAA;CACA;AACA;EACA,sBAAA;EACA,uBAAA;EACA,kBAAA;EACA,YAAA;EACA,uBAAA;EACA,mBAAA;EACA,0BAAA;CACA;AAEA;EACA,yBAAA;MAAA,sBAAA;UAAA,wBAAA;EACA,0BAAA;MAAA,uBAAA;UAAA,oBAAA;EACA,qBAAA;EAAA,qBAAA;EAAA,cAAA;EACA,uBAAA;;EAEA,oBAAA;EACA,iBAAA;EACA,8BAAA;CACA;AACA;EACA,eAAA;EACA,sBAAA;CACA;AACA;EACA,kBAAA;CACA;AACA;EACA,gBAAA;EACA,iBAAA;EACA,aAAA;EACA,YAAA;EACA,eAAA;EACA,kBAAA;EACA,oBAAA;EACA,SAAA;EACA,UAAA;EACA,gBAAA;EACA,sBAAA;CACA","file":"verify.vue","sourcesContent":["<!--verify 验证-->\n<template>\n  <div class=\"verify\">\n    <div class=\"header\">\n      <img :src=\"banner\" />\n    </div>\n    <div class=\"verify-content\" v-if=\"verifyWay\">\n      <div class=\"verify-warpper\">\n        <p class=\"verify-warpper-title\">手机验证</p>\n        <div class=\"form-warpper\">\n          <input\n            class=\"inputall phoneInput phoneInputWay\"\n            type=\"tel\"\n            maxlength=\"11\"\n            @input=\"handleChangeHave\"\n            @blur=\"handleChangeBlur\"\n            v-model=\"havePhone\"\n            placeholder=\"输入您的手机号码\"\n          />\n        </div>\n        <div class=\"verify-list-next verify-list-nextway\" @click=\"handleNext\">下一步</div>\n      </div>\n      <div class=\"footer-title\">\n        <img src=\"../../assets/image/jointforce.png\" alt />\n        <div class=\"footer-title-text\">\n          <p>页面由中软国际解放号免费提供</p>\n          <p></p>\n          <p>www.jfh.com</p>\n        </div>\n      </div>\n    </div>\n    <div class=\"verify-content\" v-else>\n      <div v-if=\"showChildPage\">\n        <div class=\"verify-warpper\">\n          <p class=\"verify-warpper-title\">手机验证</p>\n          <el-form ref=\"form\" :model=\"form\" label-width=\"80px\">\n            <el-form-item label label-width=\"0\">\n              <div class=\"form-warpper\">\n                <input\n                  class=\"inputall phoneInput\"\n                  type=\"tel\"\n                  maxlength=\"11\"\n                  @input=\"handleChangeInput\"\n                  @blur=\"handleChange\"\n                  v-model=\"form.phone\"\n                  placeholder=\"输入您的手机号\"\n                />\n              </div>\n            </el-form-item>\n            <el-form-item label label-width=\"0\">\n              <div class=\"verify-warpper-graphCode form-warpper\">\n                <input\n                  class=\"inputall graphCodeInput\"\n                  type=\"text\"\n                  maxlength=\"4\"\n                  v-model=\"form.graphCode\"\n                  placeholder=\"输入图形验证码\"\n                />\n                <img :src=\"imgCodeUrl\" alt @click=\"handleImgCode\" />\n              </div>\n            </el-form-item>\n            <el-form-item label label-width=\"0\">\n              <div class=\"verify-warpper-Code form-warpper\">\n                <input\n                  class=\"inputall codeInput\"\n                  type=\"text\"\n                  maxlength=\"4\"\n                  @input=\"handleCodeChange\"\n                  v-model=\"form.code\"\n                  placeholder=\"输入验证码\"\n                />\n                <p class=\"haveCode\" @click=\"handleCode\">{{ codeNum.name }}</p>\n                <p class=\"form-warpper-p\" v-show=\"showCode\">请输入正确的短信验证码</p>\n              </div>\n            </el-form-item>\n          </el-form>\n          <div class=\"verify-list-next\" @click=\"handleNext\">下一步</div>\n        </div>\n        <div class=\"footer-title\">\n          <img src=\"../../assets/image/jointforce.png\" alt />\n          <div class=\"footer-title-text\">\n            <p>页面由中软国际解放号免费提供</p>\n            <p></p>\n            <p>www.jfh.com</p>\n          </div>\n        </div>\n      </div>\n      <div v-else>\n        <verify-one\n          :channel-code=\"channelCodeSec\"\n          :qrcode-id=\"qrcodeId\"\n          :url-type=\"urlType\"\n          :child-data=\"childData\"\n          :phone=\"form.phone\"\n          v-if=\"childShow == 'A'\"\n        ></verify-one>\n        <verify-two\n          :channel-code=\"channelCodeSec\"\n          :qrcode-id=\"qrcodeId\"\n          :url-type=\"urlType\"\n          :child-data=\"childData\"\n          :phone=\"form.phone\"\n          v-else-if=\"childShow == 'B'\"\n        ></verify-two>\n        <verify-th\n          :channel-code=\"channelCodeSec\"\n          :qrcode-id=\"qrcodeId\"\n          :url-type=\"urlType\"\n          :child-data=\"childData\"\n          :phone=\"form.phone\"\n          v-else\n        ></verify-th>\n      </div>\n    </div>\n    <div class=\"messTip\" v-if=\"messTip\">{{ messTip }}</div>\n  </div>\n</template>\n<script>\nimport { util } from \"@/libs/util.js\";\nimport API from \"@/serve/index.js\";\nimport verifyOne from \"./verify/verifyOne\";\nimport verifyTwo from \"./verify/verifyTwo\";\nimport verifyTh from \"./verify/verifyTh\";\nexport default {\n  data() {\n    return {\n      imgCodeUrl: \"\", // 获取图像验证码的路径\n      codeNum: { name: \"获取验证码\" },\n      showCode: \"\",\n      verifyWay: false,\n      showChildPage: true,\n      docmHeight: document.documentElement.clientHeight, //默认屏幕高度\n      showHeight: document.documentElement.clientHeight, //实时屏幕高度\n      hidshow: true,\n      pageShow: false,\n      messTip: \"\",\n      showClick: false,\n      havePhone: \"\",\n      childShow: \"A\",\n      childData: {\n        title: \"抗冠战役\",\n        banner: \"\"\n      },\n      form: {\n        // phone:'18726046617',\n        // phone:'19951977014',\n        phone: \"\",\n        graphCode: \"\",\n        code: \"\"\n      },\n      timeout: true,\n      banner: require(\"../../assets/image/headerTitle.png\"),\n      titlePage: \"抗冠战役\",\n      channelCodeSec: \"\",\n      urlType: \"\",\n      qrcodeId: \"\",\n      firstExists: \"\", //初次登录\n      isRegister: false, // 是否注册\n    };\n  },\n  components: {\n    verifyOne,\n    verifyTwo,\n    verifyTh\n  },\n  created() {\n    let channelCodeSec = this.$route.query.channelCodeSe;\n\n    util.setTitle(\"手机验证\");\n    if (util.getCookie(\"userTel\")) {\n      this.$_cookiephone();\n    } else {\n      this.showClick = true;\n      this.verifyWay = false;\n      this.isRegister = true;\n    }\n    if (this.$route.query.homePage) {\n      this.channelCodeSec = sessionStorage.getItem(\"channelCodeSec\");\n      util.setCookie(\"channelCodeSec\", this.channelCodeSec);\n    }\n    if (channelCodeSec) {\n      this.$_init(channelCodeSec);\n      util.setCookie(\"channelCodeSec\", channelCodeSec);\n    } else if (util.getCookie(\"channelCodeSec\")) {\n      this.$_init(util.getCookie(\"channelCodeSec\"));\n    }\n    if (util.getCookie(\"channelCodeSec\")) {\n      this.channelCodeSec = util.getCookie(\"channelCodeSec\");\n    } else {\n      this.channelCodeSec = \"\";\n    }\n    // if (this.$route.query.urltype) {\n    //   this.urlType = this.$route.query.urltype;\n    // }\n    // if (this.$route.query.qrcodeId) {\n    //   this.qrcodeId = this.$route.query.qrcodeId;\n    // }\n\n    // 获取图形验证码\n    this.handleImgCode();\n  },\n  mounted() {\n    // window.onresize监听页面高度的变化\n    this.urlType = this.isRegister ? 'question' : '';\n    window.onresize = () => {\n      return (() => {\n        this.showHeight = document.body.clientHeight;\n      })();\n    };\n  },\n  watch: {\n    showHeight: function() {\n      if (this.docmHeight > this.showHeight) {\n        this.hidshow = false;\n      } else {\n        this.hidshow = true;\n      }\n    }\n  },\n  methods: {\n    //判断样式\n    $_init(value) {\n      API(\"get\", \"/yjgcrest/feiyan/channel?channelCodeSec=\" + value)\n        .then(res => {\n          if (res.data.code == \"000001\") {\n            let msg = res.data.data;\n            let data = JSON.parse(msg.theme);\n            this.banner = data.banner;\n            if (msg.channelName == \"软件谷\") {\n              this.childShow = \"B\";\n              this.childData = {\n                title: data.title,\n                banner: data.banner\n              };\n            } else if (msg.channelName == \"南京未来城\") {\n              this.childShow = \"C\";\n              this.childData = {\n                title: data.title,\n                banner: data.banner\n              };\n            } else {\n              this.childShow = \"A\";\n              this.childData = {\n                title: data.title,\n                banner: data.banner\n              };\n            }\n          }\n        })\n        .catch(error => {\n          console.log(error);\n        });\n    },\n    //图形验证码\n    handleImgCode(type) {\n      var random = Math.floor(Math.random() * 100);\n      var url = \"/yjgcrest/feiyan/getCaptcha?sm=\" + random;\n      this.imgCodeUrl =\n        \"https://\" + this.$util.getHeaderRouter().imgCodeUrl + url;\n    },\n    //手机号输入框改变\n    handleChangeInput(e) {\n      if (e.target.value == util.getCookie(\"userTel\") && this.pageShow) {\n        this.verifyWay = true;\n        this.showClick = true;\n        this.havePhone = util.getCookie(\"userTel\");\n        this.$nextTick(() => {\n          let btn = document.querySelector(\".verify-list-nextway\");\n          btn.style.background = \"#E4393C\";\n        });\n      }\n    },\n    handleChange(e) {\n      if (!/^1[3456789]\\d{9}$/.test(e.target.value)) {\n        this.messTip = \"请输入正确的手机号码\";\n        var that = this;\n        setTimeout(function() {\n          that.messTip = \"\";\n        }, 1000);\n        return false;\n      }\n    },\n    handleChangeHave(e) {\n      let btn = document.querySelector(\".verify-list-nextway\");\n      if (e.target.value.length == 11) {\n        btn.style.background = \"#E4393C\";\n        this.showClick = true;\n      } else {\n        btn.style.background = \"#eee\";\n        this.showClick = false;\n      }\n      if (e.target.value !== util.getCookie(\"userTel\")) {\n        this.verifyWay = false;\n        this.showChildPage = true;\n        this.form.phone = e.target.value;\n      } else {\n        this.verifyWay = true;\n      }\n    },\n    handleChangeBlur(e) {},\n    //获取手机验证码\n    handleCode() {\n      let that = this;\n      if (!this.form.phone) {\n        this.messTip = \"请输入正确的手机号码\";\n        setTimeout(function() {\n          that.messTip = \"\";\n        }, 1000);\n        return;\n      }\n      if (!/^1[3456789]\\d{9}$/.test(this.form.phone)) {\n        this.messTip = \"请输入正确的手机号码\";\n        setTimeout(function() {\n          that.messTip = \"\";\n        }, 1000);\n        return;\n      }\n      if (!this.form.graphCode) {\n        this.messTip = \"请输入正确的图形验证码\";\n        setTimeout(function() {\n          that.messTip = \"\";\n        }, 1000);\n        return;\n      }\n      let params = {\n        captcha: this.form.graphCode,\n        mobile: this.form.phone\n      };\n      if (!this.timeout) {\n        this.timeout = false;\n        return;\n      }\n      API(\"JSONPOST\", \"/yjgcrest/feiyan/sendSms\", params)\n        .then(res => {\n          if (res.data.status == 200 && res.data.code == \"000001\") {\n            let count = 60;\n            let timer = setInterval(() => {\n              this.timeout = false;\n              count--;\n              this.codeNum.name = \"重新发送（\" + count + \"s）\";\n              if (count <= 0) {\n                clearInterval(timer);\n                this.$set(this.codeNum, \"name\", \"获取验证码\");\n                this.timeout = true;\n                count = 60;\n              }\n            }, 1000);\n          } else {\n            this.messTip = res.data.msg;\n            this.handleImgCode();\n            setTimeout(() => {\n              this.messTip = \"\";\n            }, 1000);\n            this.timeout = true;\n          }\n        })\n        .catch(error => {\n          console.log(error);\n        });\n    },\n    //确认按钮改变状态\n    handleCodeChange(e) {\n      let btn = document.querySelector(\".verify-list-