sition:fixed;\n    left:0;\n    top:0;\n}\n.warp_dialog .mark[data-v-297e240e]{\n    width:100%;\n    height:100%;\n    background:#000;\n    opacity: 0.4;\n}\n.dialog_content_address[data-v-297e240e]{\n    width:100%;\n    height:22rem;\n    position:absolute;\n    left:0;\n    bottom:0;\n    background:#fff;\n    border-radius:0.2rem;\n}\n.dialog_content[data-v-297e240e]{\n    width:16rem;\n    height:6.2rem;\n    position:absolute;\n    left:1.375rem;\n    top:50%;\n    margin-top:-4.5rem;\n    background:#fff;\n    border-radius:0.2rem;\n}\n.state_style[data-v-297e240e]{\n    padding-top:1rem;\n    width:100%;\n}\n.state_style[data-v-297e240e] .el-radio{\n    display:block;\n    padding:0.7rem 1.7rem;\n    color:#333;\n}\n.state_style[data-v-297e240e] .el-radio__label{\n    font-size:0.7rem;\n}\n.show_selcet_address[data-v-297e240e]{\n    width:100%;\n    color:#333;\n}\n.show_selcet_address[data-v-297e240e]{\n    width:100%;\n    height:5.8rem;\n    padding:1rem 1rem;\n    -webkit-box-sizing: border-box;\n            box-sizing: border-box;\n    border-bottom: 1px solid #ccc;\n    overflow:hidden;\n}\n.show_selcet_address li[data-v-297e240e]{\n    float:left;\n    font-size:0.6rem;\n    color:#333;\n    padding:0.5rem 0.6rem;\n}\n.show_ullist_address[data-v-297e240e]{\n    width:100%;\n    height:14.2rem;\n    overflow-y:auto;\n    -webkit-box-sizing: border-box;\n            box-sizing: border-box;\n    padding:1rem;\n}\n.show_ullist_address span[data-v-297e240e]{\n    display:inline-block;\n    padding:0.3rem 0.6rem;\n    font-size:0.7rem;\n    color:#666;\n    border:1px solid #ccc;\n    border-radius:2px;\n    margin:0 0.6rem 0.4rem 0;\n}\n/**---性别 ----*/\n.common_select_state[data-v-297e240e]{\n    display:block;\n    height:2.2rem;\n    padding:0 0.85rem;\n    line-height:2.2rem;\n    font-size:0.8rem;\n    border:1px solid #e9e9e9;\n    background:#fff;\n    border-radius:4px;\n    //width:13.9rem;\n    -webkit-box-sizing: border-box;\n            box-sizing: border-box;\n    position:relative;\n}\n.common_select_state b[data-v-297e240e]{\n    font-weight:normal;\n}\n.common_select_state .arrow_right[data-v-297e240e]{\n    position:absolute;\n    right:0.4rem;\n    font-size:1rem;\n    top:50%;\n    margin-top:-0.5rem;\n}\n.common_select_state .tip[data-v-297e240e]{\n    color:#ccc8c8;\n    font-style: normal;\n}\n.footer-title[data-v-297e240e]{\n    -webkit-box-pack: center;\n        -ms-flex-pack: center;\n            justify-content: center;\n    -webkit-box-align: center;\n        -ms-flex-align: center;\n            align-items: center;\n    display: -webkit-box;\n    display: -ms-flexbox;\n    display: flex;\n    padding-bottom: 0.5rem;\n\n    margin-top:1.15rem;\n    text-align: left;\n    color:rgba(153,153,153,1);\n\n    //width:100%;\n    //position:fixed;\n    //bottom:0.9rem;\n    //left:0;\n    //text-align:center;\n}\n.footer-title img[data-v-297e240e]{\n    width: 3.25rem;\n    margin-right: 0.15rem;\n}\n.footer-title-text[data-v-297e240e]{\n    font-size:0.3rem;\n}\n.messTip[data-v-297e240e]{\n    position:fixed;\n    background:#000;\n    opacity: 0.4;\n    color:#fff;\n    height:1.6rem;\n    font-size:0.6rem;\n    line-height:1.6rem;\n    top:33%;\n    left:30%;\n    padding:0 1rem;\n    border-radius:0.2rem;\n}\n#start_date[data-v-297e240e]{\n    height: 2rem;\n    font-size: 0.8rem;\n    /* box-shadow: 0px 0px 4px 0px rgba(102,102,102,0.2); */\n    border-radius: 4px;\n    font-family: Microsoft YaHei;\n    font-weight: 400;\n    padding: 0.1rem 0 .1rem 0.85rem;\n    width: 13.9rem;\n}\n", "", {"version":3,"sources":["/Users/shanshan/Desktop/vue-project/shenbing/src/components/organization/verify/src/components/organization/verify/verifyTwo.vue"],"names":[],"mappings":";AAkdA;IACA,WAAA;IACA,aAAA;IACA,mBAAA;CACA;AACA;IACA,WAAA;IACA,8BAAA;IACA,yBAAA;IACA,+BAAA;YAAA,uBAAA;CACA;AACA;IACA,0CAAA;IACA,iBAAA;IACA,sBAAA;IACA,YAAA;IACA,iBAAA;IACA,+BAAA;YAAA,uBAAA;CACA;AACA;IACA,kBAAA;IACA,wBAAA;IACA,uBAA