exports.push([module.id, "\n.verify-warpper[data-v-5a4d85da]{\n    padding: 1.225rem 1.3rem 1.325rem 1.45rem;\n    background: #fff;\n    border-radius: 0.4rem;\n    width: 100%;\n    /*height: 65vh;*/\n    -webkit-box-sizing: border-box;\n            box-sizing: border-box;\n}\n.verify-warpper-title[data-v-5a4d85da]{\n    font-size: 0.9rem;\n    color: rgba(51,51,51,1);\n    margin-bottom: 1.35rem;\n}\n.verify-warpper-title i[data-v-5a4d85da]{\n    display:block;\n    width:0.4rem;\n    height:0.4rem;\n    border-radius:50%;\n    background:#3366CC;\n    float:left;\n    margin-top:0.45rem;\n}\n.verify-warpper-title span[data-v-5a4d85da]{\n    margin-left: 0.4rem;\n}\n.form-warpper[data-v-5a4d85da]{\n    position: relative;\n}\n.verify-warpper .form-warpper-p[data-v-5a4d85da]{\n    position: absolute;\n    color: red;\n    bottom: -15px;\n}\n.phoneInput[data-v-5a4d85da]{\n    width: 13.9rem;\n}\n.graphCodeInput[data-v-5a4d85da]{\n     width: 8.513rem;\n}\n.codeInput[data-v-5a4d85da]{\n    width: 13.9rem;\n}\n.verify-warpper-graphCode[data-v-5a4d85da]{\n    display: -webkit-box;\n    display: -ms-flexbox;\n    display: flex;\n    -webkit-box-pack: justify;\n        -ms-flex-pack: justify;\n            justify-content: space-between;\n    -webkit-box-align: center;\n        -ms-flex-align: center;\n            align-items: center;\n}\n.verify-warpper-graphCode img[data-v-5a4d85da] {\n    height: 1.375rem;\n    width: 4.5rem;\n    display: block;\n}\n.verify-warpper-Code[data-v-5a4d85da]{\n    position: relative;\n}\n.haveCode[data-v-5a4d85da]{\n    position: absolute;\n    right: .5rem;\n    top: .2rem;\n    color: #3366CC;\n    font-size: 0.8rem;\n}\n.inputall[data-v-5a4d85da]{\n    height: 2rem;\n    font-size: 0.8rem;\n    /*box-shadow: 0px 0px 4px 0px rgba(102,102,102,0.2);*/\n    border-radius: 4px;\n    font-family: Microsoft YaHei;\n    font-weight: 400;\n    padding: 0.1rem 0 .1rem 0.85rem;\n}\n.phoneInputWay[data-v-5a4d85da]{\n    height:2.125rem;\n}\n.verify-list-next[data-v-5a4d85da]{\n    text-align: center;\n    width: 14.75rem;\n    height: 2.5rem;\n    margin: 0 auto;\n    line-height: 2.5rem;\n    background: #eee;\n    font-size: 1rem;\n    color: #fff;\n    font-family: Microsoft YaHei;\n    font-weight: 400;\n    border-radius: 1.25rem;\n    margin-top:2.5rem;\n}\n.verify-list-nextway[data-v-5a4d85da]{\n    margin-top:60%;\n    background: #E4393C;\n}\n.erify-list-nextred[data-v-5a4d85da]{\n    background: #E4393C;\n}\n//form表单\n.form_title[data-v-5a4d85da]{\n    font-size:0.75rem;\n    line-height:0.9rem;\n    padding-top:1.2rem;\n}\n.verify-warpper .el-form-item[data-v-5a4d85da]{\n    padding:0;\n    margin-bottom: 1rem;\n}\n.verify-warpper .input_style[data-v-5a4d85da]{\n    margin-top:0.7rem;\n}\n.verify-warpper .input_style[data-v-5a4d85da] .el-input__inner{\n    height:2.75rem;\n    padding:0 0.7rem;\n    line-height:2.75rem;\n    font-size:0.8rem;\n}\n.form_title i[data-v-5a4d85da]{\n    color:#f00;\n    margin-left:0.4rem;\n}\n.address_state[data-v-5a4d85da]{\n    display:block;\n    padding:0.4rem 0.7rem;\n    line-height:1.2rem;\n    font-size:0.8rem;\n    border:1px solid #e9e9e9;\n    background:#fff;\n    border-radius:4px;\n    width:14.75rem;\n    -webkit-box-sizing: border-box;\n            box-sizing: border-box;\n    position:relative;\n}\n.address_state b[data-v-5a4d85da]{\n    font-weight:normal;\n}\n.address_state .arrow_right[data-v-5a4d85da]{\n    position:absolute;\n    right:0.4rem;\n    font-size:1rem;\n    top:50%;\n    margin-top:-0.5rem;\n}\n.address_state .tip[data-v-5a4d85da]{\n    color:#ccc8c8;\n    font-style: normal;\n}\n.warp_dialog[data-v-5a4d85da]{\n    width:100%;\n    height:100%;\n    position:fixed;\n    left:0;\n    top:0;\n}\n.warp_dialog .mark[data-v-5a4d85da]{\n    width:100%;\n    height:100%;\n    background:#000;\n    opacity: 0.4;\n}\n.dialog_content_address[data-v-5a4d85da]{\n    width:100%;\n    height:22rem;\n    position:absolute;\n    left:0;\n    bottom:0;\n    background:#fff;\n    border-radius:0.2rem;\n}\n.show_selcet_address[data-v-5a4d85da]{\n    width:100%;\n    color:#333;\n}\n.show_selcet_address[data-v-5a4d85da]{\n    width:100%;\n    height:5.8rem;\n    padding:1rem 1rem;\n    -webkit-box-sizing: border-box;\n            box-sizing: border-box;\n    border-bottom: 1px solid #ccc;\n    overflow:hidden;\n}\n.show_selcet_address li[data-v-5a4d85da]{\n    float:left;\n    font-size:0.6rem;\n    color:#333;\n    padding:0.5rem 0.6rem;\n}\n.show_ullist_address[data-v-5a4d85da]{\n    width:100%;\n    height:14.2rem;\n    overflow-y:auto;\n    -webkit-box-sizing: border-box;\n            box-sizing: border-box;\n    padding:1rem;\n}\n.show_ullist_address span[data-v-5a4d85da]{\n    display:inline-block;\n    padding:0.3rem 0.6rem;\n    font-size:0.7rem;\n    color:#666;\n    border:1px solid #ccc;\n    border-radius:2px;\n    margin:0 0.6rem 0.4rem 0;\n}\n.messTip[data-v-5a4d85da]{\n    position:fixed;\n    background:#000;\n    opacity: 0.4;\n    color:#fff;\n    height:1.6rem;\n    font-size:0.6rem;\n    line-height:1.6rem;\n    top:33%;\n    left:30%;\n    padding:0 1rem;\n    border-radius:0.2rem;\n}\n.footer-title[data-v-5a4d85da]{\n    -webkit-box-pack: center;\n        -ms-flex-pack: center;\n            justify-content: center;\n    -webkit-box-align: center;\n        -ms-flex-align: center;\n            align-items: center;\n    display: -webkit-box;\n    display: -ms-flexbox;\n    display: flex;\n    padding-bottom: 0.5rem;\n\n    margin-top:1.15rem;\n    text-align: left;\n    color:rgba(153,153,153,1);\n\n    //width:100%;\n    //position:fixed;\n    //bottom:0.9rem;\n    //left:0;\n    //text-align:center;\n}\n.footer-title img[data-v-5a4d85da]{\n    width: 3.25rem;\n    margin-right: 0.15rem;\n}\n.footer-title-text[data-v-5a4d85da]{\n    font-size:0.3rem;\n}\n", "", {"version":3,"sources":["/Users/shanshan/Desktop/vue-project/shenbing/src/components/organization/verify/src/components/organization/verify/verifyOne.vue"],"names":[],"mappings":";AAkSA;IACA,0CAAA;IACA,iBAAA;IACA,sBAAA;IACA,YAAA;IACA,iBAAA;IACA,+BAAA;YAAA,uBAAA;CACA;AACA;IACA,kBAAA;IACA,wBAAA;IACA,uBAAA;CACA;AACA;IACA,cAAA;IACA,aAAA;IACA,cAAA;IACA,kBAAA;IACA,mBAAA;IACA,WAAA;IACA,mBAAA;CACA;AACA;IACA,oBAAA;CACA;AACA;IACA,mBAAA;CACA;AACA;IACA,mBAAA;IACA,WAAA;IACA,cAAA;CACA;AACA;IACA,eAAA;CACA;AAEA;KACA,gBAAA;CACA;AACA;IACA,eAAA;CACA;AACA;IACA,qBAAA;IAAA,qBAAA;IAAA,cAAA;IACA,0BAAA;QAAA,uBAAA;YAAA,+BAAA;IACA,0BAAA;QAAA,uBAAA;YAAA,oBAAA;CACA;AACA;IACA,iBAAA;IACA,cAAA;IACA,eAAA;CACA;AACA;IACA,mBAAA;CACA;AACA;IACA,mBAAA;IACA,aAAA;IACA,WAAA;IACA,eAAA;IACA,kBAAA;CACA;AACA;IACA,aAAA;IACA,kBAAA;IACA,sDAAA;IACA,mBAAA;IACA,6BAAA;IACA,iBAAA;IACA,gCAAA;CACA;AACA;IACA,gBAAA;CACA;AACA;IACA,mBAAA;IACA,gBAAA;IACA,eAAA;IACA,eAAA;IACA,oBAAA;IACA,iBAAA;IACA,gBAAA;IACA,YAAA;IACA,6BAAA;IACA,iBAAA;IACA,uBAAA;IACA,kBAAA;CACA;AACA;IACA,eAAA;IACA,oBAAA;CACA;AACA;IACA,oBAAA;CACA;AACA;;IAEA,kBAAA;IACA,mBAAA;IACA,mBAAA;CACA;AACA;IACA,UAAA;IACA,oBAAA;CACA;AACA;IACA,kBAAA;CACA;AACA;IACA,eAAA;IACA,iBAAA;IACA,oBAAA;IACA,iBAAA;CACA;AACA;IACA,WAAA;IACA,mBAAA;CACA;AACA;IACA,cAAA;IACA,sBAAA;IACA,mBAAA;IACA,iBAAA;IACA,yBAAA;IACA,gBAAA;IACA,kBAAA;IACA,eAAA;IACA,+BAAA;YAAA,uBAAA;IACA,kBAAA;CACA;AACA;IACA,mBAAA;CACA;AACA;IACA,kBAAA;IACA,aAAA;IACA,eAAA;IACA,QAAA;IACA,mBAAA;CACA;AACA;IACA,cAAA;IACA,mBAAA;CACA;AACA;IACA,WAAA;IACA,YAAA;IACA,eAAA;IACA,OAAA;IACA,MAAA;CACA;AACA;IACA,WAAA;IACA,YAAA;IACA,gBAAA;IACA,aAAA;CACA;AACA;IACA,WAAA;IACA,aAAA;IACA,kBAAA;IACA,OAAA;IACA,SAAA;IACA,gBAAA;IACA,qBAAA;CACA;AACA;IACA,WAAA;IACA,WAAA;CACA;AACA;IACA,WAAA;IACA,cAAA;IACA,kBAAA;IACA,+BAAA;YAAA,uBAAA;IACA,8BAAA;IACA,gBAAA;CACA;AACA;IACA,WAAA;IACA,iBAAA;IACA,WAAA;IACA,sBAAA;CACA;AACA;IACA,WAAA;IACA,eAAA;IACA,gBAAA;IACA,+BAAA;YAAA,uBAAA;IACA,aAAA;CACA;AACA;IACA,qBAAA;IACA,sBAAA;IACA,iBAAA;IACA,WAAA;IACA,sBAAA;IACA,kBAAA;IACA,yBAAA;CACA;AACA;IACA,eAAA;IACA,gBAAA;IACA,aAAA;IACA,WAAA;IACA,cAAA;IACA,iBAAA;IACA,mBAAA;IACA,QAAA;IACA,SAAA;IACA,eAAA;IACA,qBAAA;CACA;AACA;IACA,yBAAA;QAAA,sBAAA;YAAA,wBAAA;IACA,0BAAA;QAAA,uBAAA;YAAA,oBAAA;IACA,qBAAA;IAAA,qBAAA;IAAA,cAAA;IACA,uBAAA;;IAEA,mBAAA;IACA,iBAAA;IACA,0BAAA;;IAEA,aAAA;IACA,iBAAA;IACA,gBAAA;IACA,SAAA;IACA,oBAAA;CACA;AACA;IACA,eAAA;IACA,sBAAA;CACA;AACA;IACA,iBAAA;CACA","file":"verifyOne.vue","sourcesContent":["\n<template>\n    <div>\n       <div class='verify-warpper'>\n            <p class='verify-warpper-title'><i></i><span>{{childData.title}}</span> </p>\n            <el-form ref=\"form\" :model=\"form\" label-width=\"80px\">\n                <el-form-item label=\"\" label-width=\"0\">\n                    <p class=\"form_title color333\"><span>1.请输入您的姓名</span><i>*</i></p>\n                    <input class='inputall phoneInput' type=\"text\" @input=\"handleCodeChange\"  v-model='form.name' placeholder=\"请填写\">\n                </el-form-item>\n                <el-form-item label=\"\" label-width=\"0\">\n                    <p class=\"form_title color333\"><span>2.请输入您的身份证号码</span><i>*</i></p>\n                    <input class='inputall phoneInput' type=\"text\" @blur='handleCard' @input=\"handleCodeChange\"  v-model='form.identityCard' placeholder=\"请填写\">\n                </el-form-item>\n                <el-form-item label=\"\" label-width=\"0\">\n                    <p class=\"form_title color333\"><span>3.请输入您的常驻地址（省市）</span><i>*</i></p>\n                    <span class=\"address_state\" @click=\"openAddressList\">\n                        <i class=\"tip\" v-if=\"!isShowAddressList && selectCityArr.length == 0\">请填写</i>\n                        <span v-else><b v-for=\"(item,index) in selectCityArr\" :key=\"index\">{{item.name}}<br v-if=\"index!=selectCityArr.length-1\"/></b></span>\n                        <i class=\"el-icon-arrow-right arrow_right\"></i>\n                    </span>\n                </el-form-item>\n            </el-form>\n            <div class='verify-list-next' @click='handleNext'>下一步</div>\n        </div> \n        <div class='footer-title'>\n            <img src=\"../../../assets/image/jointforce.png\" alt=\"\">\n            <div class='footer-title-text'>\n                <p>页面由中软国际解放号免费提供<p>\n                <p>www.jfh.com</p>\n            </div>\n        </div>\n        <!--选择城市-->\n        <div class=\"warp_dialog\" v-if=\"isShowAddressList\" @click=\"closeAddressList\">\n            <div class=\"mark\"></div>\n            <div class=\"dialog_content_address\" @click.stop=\"function(){return false}\">\n                <ul class=\"show_selcet_address\">\n                    <p @click=\"getAddressList('')\">选择城市</p>\n                    <li v-for=\"(item,index) in selectCityArr\" :key=\"index\" @click=\"getAddressList(item.code,index)\">{{item.name}}</li>\n                </ul>\n                <div class=\"show_ullist_address\" v-if=\"firstCityList\">\n                   <span v-for=\"(item,index) in firstCityList\" :key=\"index\" @click=\"selectCity(item.fullName,item.parentCode,item.code)\">{{item.fullName}}</span>\n                </div>\n            </div>\n        </div>\n        <div class=\"messTip\" v-if=\"messTip\">{{messTip}}</div>\n    </div>\n</template>\n<script>\n    import {util} from '@/libs/util.js';\n    import API from '@/serve/index.js';\n    export default{\n        props:{\n            childData:{},\n            phone:'',\n            channelCode:'',\n            urlType:'',\n            qrcodeId:'',\n        },\n        data(){\n            return{\n                havePhone:'',\n                graphCode:'',\n                code:'',\n                messTip:'',\n                showPhone:false,\n                showGraphCode:false,\n                url:'',\n                showCode:'',\n                showClick:false,\n                \n                \n                form:{\n                    name:'',\n                    identityCard:'',\n                    address:'', // 住址\n                },\n                isShowAddressList:false,\n                firstCityList:[], // 一级城市\n                selectCityArr:[], // 选择过的城市\n                judeCode:'',\n            }\n        },\n        watch:{\n            selectCityArr(nv,ov){\n                this.handleCodeChange()\n            },\n        },\n        created(){\n            // 获取地区联动\n            this.getAddressList();\n        },\n        methods:{\n             // 选择住址\n            openAddressList(){\n                this.isShowAddressList = true;\n            },\n             // 关闭选择住址的弹框\n            closeAddressList:function(){\n                this.isShowAddressList = false;\n            },\n             // 获取地区联动\n            getAddressList(code,index){\n                var that = this;\n                if(this.selectCityArr.length > 0){\n                    if(index == 0 || index){\n                        const { selectCityArr } = this;\n                        this.selectCityArr = selectCityArr.filter((n, i) => i <= index);\n                    }\n                }\n                var params = '';\n                if(code){\n                    params = {'parentCode':code} ;\n                }else{\n                    this.selectCityArr = [];\n                    params = ''\n                }\n                API('get','/yjgcrest/feiyan/findAreaInfo',params).then(result => {\n                    if(result.status == 200){ //成功\n                        this.judeCode = '';\n                        if(result.data.data.data.length > 0){\n                            that.firstCityList = result.data.data.data;\n                        }else{\n                            var length = that.selectCityArr.length - 1;\n                            if(that.selectCityArr.length > 1){\n                                if(that.selectCityArr[length - 1].parentCode == that.selectCityArr[length].parentCode){\n                                    const { selectCityArr } = that;\n                                    that.selectCityArr = selectCityArr.filter((n, i) => i != length - 1);\n                                }\n                            }\n                            that.closeAddressList();\n                        }\n                    }\n                })\n            },\n             // 选择过的城市\n            selectCity:function(name,parentCode,code){\n                if(this.judeCode == code){\n                    return\n                }\n                this.judeCode = code;\n                this.selectCityArr.push({\n                    name:name,\n                    parentCode:parentCode,\n                    code:code,\n                });\n                if(parentCode){\n                    this.getAddressList(code);\n                }\n            },\n            //身份证\n            handleCard(e){\n                var reg = /^[1-9]\\d{5}(18|19|([2]\\d))\\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\\d{3}[0-9Xx]$/\n                //var reg = /(^\\d{15}$)|(^\\d{18}$)|(^\\d{17}(\\d|X|x)$)/;\n                let year = e.target.value.slice(6,10);\n                let month = e.target.value.slice(10,12);\n                let date = e.target.value.slice(12,14);\n                let newYear = new Date().getFullYear();\n                let newMonth = new Date().getMonth()+1;\n                let newDate = new Date().getDate(); \n                if(year == newYear){\n                    if(month > newMonth){\n                         this.messTip = '请输入正确的身份证号码';\n                        setTimeout(function(){\n                            that.messTip = '';\n                        },1000);\n                        return false; \n                    }\n                    if(month == newMonth){\n                        if(date > newDate){\n                             this.messTip = '请输入正确的身份证号码';\n                            setTimeout(function(){\n                                that.messTip = '';\n                            },1000);\n                            return false; \n                        }\n                    }\n                    \n                }\n                if(reg.test(e.target.value) === false  || (year > newYear) )  {\n                    this.messTip = '请输入正确的身份证号码';\n                    var that = this;\n                    setTimeout(function(){\n                        that.messTip = '';\n                    },1000);\n                    return false; \n                }\n            },\n            handleCodeChange(e){\n                let btn = document.querySelector('.verify-list-next');\n                if(this.form.name != '' && this.form.identityCard !='' && this.selectCityArr.length > 0){\n                    this.showClick = true;\n                    btn.style.background = '#E4393C';\n                }else{\n                    btn.style.background = '#eee';\n                    this.showClick = false;\n                }\n            },\n            \n            \n            //下一步\n            handleNext(){\n                if(!this.showClick){\n                    return;\n                }\n                let that = this;\n                    if(!this.form.name){\n                        this.messTip = '请输入您的姓名';\n                        setTimeout(function(){\n                            that.messTip = '';\n                        },1000);\n                        return;\n                    };\n                    if(!this.form.identityCard){\n                        this.messTip = '请输入您的身份证号码';\n                        setTimeout(function(){\n                            that.messTip = '';\n                        },1000);\n                        return;\n                    }\n                    var reg = /^[1-9]\\d{5}(18|19|([2]\\d))\\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\\d{3}[0-9Xx]$/;  \n                    if(reg.test(this.form.identityCard) === false)  {\n                        this.messTip = '请输入正确的身份证号码';\n                        setTimeout(function(){\n                            that.messTip = '';\n                        },1000);\n                        return; \n                    }\n                    if(this.selectCityArr.length==0){\n                        this.messTip = '请选择您的常驻地址';\n                        setTimeout(function(){\n                            that.messTip = '';\n                        },1000);\n                        return;\n                    }\n                    let address = '';\n                    if(this.selectCityArr.length > 0){\n                        for(var i = 0; i < this.selectCityArr.length; i ++){\n                            address += this.selectCityArr[i].name;\n                        }\n                    }\n\n                    let params = {\n                        mobile:this.phone,\n                        identityNumber:this.form.identityCard,\n                        oftenProvicne:address,\n                        userName:this.form.name,\n                        sex:'',\n                        birthday:'',\n                        companyId:'',\n                        departmentName:'',\n                        address:'',\n                    };\n                    //新增查验userTel的设置，防止tooken丢失电话\n                    // util.setCookie('userTel',this.phone)\n                    // console.log('填报姓名，地址后==》verityOne==getcookie==userTel==>', util.getCookie('userTel'))\n                    API('JSONPOST','/yjgcrest/feiyan/editvisitor',params).then( res => {\n                        if(res.data.status == 200 && res.data.code == '000001'){\n                            util.setCookie('userTel',this.phone)\n                            util.setCookie('userName',this.form.name)\n                            var channelCodese = sessionStorage.getItem('channelCodeSec');\n                            if(this.urlType){\n                                // this.$router.push({ path: '/entryNumber/index', query: {qrcodeId:this.qrcodeId,channelCodeSe:this.channelCode} });\n                                this.$router.push({ path: '/entryNumber/index', query: {qrcodeId:this.qrcodeId,channelCodeSe:channelCodese}});\n                            }else{\n                                if(res.data.data.qrcodeId && res.data.data.qrcodeId != \"\"){\n                                    // this.$router.push({ path: '/organization', query: {phone:this.phone,channelCodeSe:this.channelCode} });\n                                    this.$router.push({path: '/organization', query:{phone:this.phone,channelCodeSe:channelCodese}});\n                                }else{\n                                    // this.$router.push({ path: '/createCode', query: {phone:this.phone,channelCodeSe:this.channelCode} });\n                                    this.$router.push({path: '/createCode',query:{phone:this.phone,channelCodeSe:channelCodese}});\n                                }\n                            }\n                        }else{\n                            this.messTip = res.data.msg;\n                            var that = this;\n                            setTimeout(function(){\n                                that.messTip = '';\n                            },1000);\n                        }\n                    }).catch( error => {\n                        console.log(error)\n                    })\n            },\n\n        },\n    }\n\n</script>\n<style scoped>\n    .verify-warpper{\n        padding: 1.225rem 1.3rem 1.325rem 1.45rem;\n        background: #fff;\n        border-radius: 0.4rem;\n        width: 100%;\n        /*height: 65vh;*/\n        box-sizing: border-box;\n    }\n    .verify-warpper-title{\n        font-size: 0.9rem;\n        color: rgba(51,51,51,1);\n        margin-bottom: 1.35rem;\n    }\n    .verify-warpper-title i{\n        display:block;\n        width:0.4rem;\n        height:0.4rem;\n        border-radius:50%;\n        background:#3366CC;\n        float:left;\n        margin-top:0.45rem;\n    }\n    .verify-warpper-title span{\n        margin-left: 0.4rem;\n    }\n   